"use client";

import { useEffect } from "react";
import { usePathname, useSearchParams } from "next/navigation";
import { useGlobalStore, Theme } from "@/lib/store";

const colors = {
  teal: {
    primary: "#457E7F",
    light: "rgba(69, 126, 127, 0.1)",
    dark: "rgba(59, 108, 109, 0.9)",
  },
  orange: {
    primary: "#E36A20",
    light: "rgba(227, 106, 32, 0.1)",
    dark: "rgba(193, 90, 27, 0.9)",
  },
};

// Define theme for each route
const getThemeForPath = (pathname: string, searchParams: URLSearchParams): Theme => {
  // Products page - check URL param
  if (pathname === "/products") {
    const urlTheme = searchParams.get("theme") as Theme | null;
    if (urlTheme === "orange") return "orange";
    return "teal"; // default
  }

  // WW brown rice is orange
  if (pathname === "/products/ww-brown-rice") {
    return "orange";
  }

  // Everything else is teal
  return "teal";
};

export function ThemeProvider({ children }: { children: React.ReactNode }) {
  const pathname = usePathname();
  const searchParams = useSearchParams();
  const { theme, setTheme } = useGlobalStore();

  // Set theme based on path
  useEffect(() => {
    const newTheme = getThemeForPath(pathname, searchParams);
    setTheme(newTheme);
  }, [pathname, searchParams, setTheme]);

  // Apply CSS variables
  useEffect(() => {
    document.documentElement.style.setProperty("--primary", colors[theme].primary);
    document.documentElement.style.setProperty("--primary-light", colors[theme].light);
    document.documentElement.style.setProperty("--primary-dark", colors[theme].dark);
  }, [theme]);

  return <>{children}</>;
}
